<template>
  <div class="main-div">
    <br />
    <b-col>
      <h2>
        Post data to Loan and leave history files and close month
      </h2>
    </b-col>
    <b-row class="mt-1">
      <b-col cols="2" offset="5">
        <b-button class="button-style" variant="success" @click="closeMonth"
          >Submit</b-button
        >
      </b-col>
    </b-row>
    <div v-show="isBusy">
      <template>
        <div class="text-center text-danger my-2">
          <b-spinner class="align-middle"></b-spinner>
          <h4><strong>Processing, please wait...</strong></h4>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import axios from 'axios'
export default {
  data () {
    return {
      isBusy: false
    }
  },
  computed: {
    ...mapGetters([]),
    total: function () {
      return parseFloat(this.t1 || 0) + parseFloat(this.t2 || 0)
    }
  },
  methods: {
    ...mapActions([]),
    closeMonth () {
      this.isBusy = !this.isBusy
      this.processCloseMonth()
    },
    async processCloseMonth () {
      // console.log('Closing month')
      const url = process.env.VUE_APP_API_URL + '/monthEndClose'
      await axios
        .post(url, {
          headers: {
            'Content-Type': 'application/json'
          }
        })
        .then(response => {
          this.msg = {}
          //   commit('setMessage', 'Sucess')
          this.isBusy = !this.isBusy
          this.$toasted.show('Close month process completed sucessfully!', {
            theme: 'bubble',
            position: 'top-right',
            duration: 5000
          })
        })
        .catch(error => {
          console.log(error.response.data)
          this.msg = 'Error:' + JSON.stringify(error.response.data.errorMessage)
        })
    }
  }
}
</script>

<style scoped>
.main-div {
  width: 95%;
  margin-left: 40px;
  margin-right: 40px;
  font-size: small;
}
.center {
  text-align: center;
  /* border: 3px solid green; */
}
.col-right {
  text-align: right;
}
.col-left {
  text-align: left;
}
.div-border {
  border: 5cm;
}
input:invalid {
  border-color: red;
  background-color: rgb(243, 243, 203);
}
input:valid {
  border-color: green;
}
.error-message {
  display: block;
}
</style>
